<template>
  <div>
       <p class="pl-4 font-bold font-times">Nama Sekolah :</p>
      <hr class="style16">
      <div class="sm-form ">
          <label for="nama_sekolah">Nama Sekolah : </label>
          <input type="text" id="nama_sekolah" name="nama_sekolah" class="form-control form-control-sm" placeholder="nama_sekolah" v-model="vdata.nama_sekolah" >
      </div>
   <button type="button" @click="sekolahs" class="btn btn-sm btn-dark  float-right font-times"><span class="typcn typcn-edit"></span> Simpan</button>
      <p class="pl-4 font-bold font-times">Visi dan Misi :</p>
      <hr class="style16">
   <vue-editor v-model="visi"></vue-editor>
   <br>
   <button type="button" @click="visis" class="btn btn-sm btn-dark  float-right font-times"><span class="typcn typcn-edit"></span> Simpan</button>
   <br>
      <p class="pl-4 font-bold font-times">Sejarah Sekolah : </p>
      <hr class="style16">
   <vue-editor v-model="sejarah"></vue-editor>
   <br>
   <button type="button" @click="sejarahs" class="btn btn-sm btn-dark  float-right font-times"><span class="typcn typcn-edit"></span> Simpan</button>
   <br>
      <p class="pl-4 font-bold font-times">Logo Sekolah : </p>
      <hr class="style16">
    <input type="file" name="file" @change="compress($event)" />
    <br />
    <button class="btn btn-sm btn-primary" :disabled='!ready' @click.prevent="inputfile">Simpan</button>   
    <img :src="`https://infolayanans.space/${this.gambar}`" class="kinoLightBox img-fluid rounded-top z-depth-2" style="border-radius: 20px;" alt="top">
    
    <hr class="style5">
     <br>
      <p class="pl-4 font-bold font-times">Kepala Sekolah : </p>
      <hr class="style16">
    <input type="file" name="file" @change="compress($event)" />
    <br />
    <button class="btn btn-sm btn-primary" :disabled='!ready' @click.prevent="inputfile2">Simpan</button>   
    <img :src="`https://infolayanans.space/${this.gambar2}`" class="kinoLightBox img-fluid rounded-top z-depth-2" style="border-radius: 20px;" alt="top">
    <div class="sm-form ">
        <label for="kepsek">Nama Kepala Sekolah : </label>
        <input type="text" id="kepsek" name="kepsek" class="form-control form-control-sm" placeholder="kepsek" v-model="vdata.kepsek" >
    </div>
   <button type="button" @click="kepalas" class="btn btn-sm btn-dark  float-right font-times"><span class="typcn typcn-edit"></span> Simpan</button>
     <br>
      <p class="pl-4 font-bold font-times">Sambutan dari Kepala sekolah : </p>
      <hr class="style16">
      <vue-editor v-model="sambutan"></vue-editor>
      <br>
   <button type="button" @click="sambutans" class="btn btn-sm btn-dark  float-right font-times"><span class="typcn typcn-edit"></span> Simpan</button>
   <br>
  </div>
</template>
<script>
import axios from 'axios'
import { VueEditor } from "vue2-editor";
export default {
    layout:"guru",
  components: {
    VueEditor
  },
  mounted() {
      let fd = new FormData()
      fd.append('data', `select * from sekolah`)//database setting di server
      fd.append('database','infolay3_test')
      axios.post('https://infolayanans.space/api/mysql/auto.php', fd
      , {
          headers: {
          Authorization: localStorage.getItem('auth._token.local')
          }
      }).then(res => {
          this.visi=res.data[0].visi
          this.sejarah=res.data[0].sejarah
          this.gambar=res.data[0].gambar
          this.vdata.nama_sekolah=res.data[0].nama
          this.vdata.kepsek=res.data[0].kepala_sekolah
          this.gambar2=res.data[0].gambar_kepsek
          this.sambutan=res.data[0].sambutan
          this.$forceUpdate()
      })
  },
  methods: {
      sekolahs(){
        let fd = new FormData()
        fd.append('data', `update sekolah set nama='${this.vdata.nama_sekolah}' where id_sekolah='1'`)//database setting di server
        fd.append('database','infolay3_test')
        axios.post('https://infolayanans.space/api/mysql/auto.php', fd
        , {
            headers: {
            Authorization: localStorage.getItem('auth._token.local')
            }
        }).then(res => {
            console.log(res)
            alert("simpan Nama sekolah berhasil")
            this.$forceUpdate()
        })
      },
        getFile(e){ this.file=e.target.files[0] },       
       compress(event) {
         // this.imgList=e.target.files;
         if (event.target.files[0].size > 10000000) {
           alert('File yang di upload tidak sesuai kriteria')
         } else {
           if (event.target.files.length > 0) {
             let img = []
             let hasil = []
             let compress = 'null'
             let filenya
             for (let i = 0; i < event.target.files.length; i++) {
               this.imgs.push(event.target.files[i])
               var selectedFile = event.target.files[i]
               var reader = new FileReader()
               reader.onload = function(event) {
                 //   img.push(event.target.result)
               }
               reader.readAsDataURL(selectedFile)
               filenya = event.target.files[i]
               compress =
                 filenya.size < 1000000
                   ? 30
                   : filenya.size < 2000000
                     ? 25
                     : filenya.size < 3000000
                       ? 20
                       : filenya.size < 4000000
                         ? 15
                         : 10
               this.$daycaca.compress(filenya, compress, data => {
                 this.gambar = data
                 img.push(data)
                 this.$urltofile(data, filenya.name, filenya.type).then(res => {
                   hasil.push(res)
                 })
               })
             }
             console.log('base64',img)
             setTimeout(() => {
                console.log('file',hasil[0])
                 this.file = hasil[0]
                 this.ready=true
             }, 1000);
           //   this.$emit('upload', hasil)
           }
         }
       },
       inputfile() {
         let fd = new FormData();
         fd.append("file", this.file);
         fd.append("id", this.$store.state.users.id_guru);
         axios
           .post("https://infolayanans.space/api/mysql/upload.php", fd, {
             headers: {
               "content-type": "multipart/form-data"
             }
           })
           .then(res => {
             console.log(res); 
               let fd2 = new FormData()
                fd2.append('data', `update sekolah set gambar='${res.data}' where id_sekolah='1'`)//database setting di server
                fd2.append('database','infolay3_test')
                axios.post('https://infolayanans.space/api/mysql/auto.php', fd2
                , {
                    headers: {
                    Authorization: localStorage.getItem('auth._token.local')
                    }
                }).then(res => {
                    console.log(res)
                    alert("simpan gambar berhasil")
                    this.$forceUpdate()
                })
                    // get /api/gambar/url.jpg
                });
       },
         inputfile2() {
         let fd = new FormData();
         fd.append("file", this.file);
         fd.append("id", this.$store.state.users.id_guru);
         axios
           .post("https://infolayanans.space/api/mysql/upload.php", fd, {
             headers: {
               "content-type": "multipart/form-data"
             }
           })
           .then(res => {
             console.log(res); 
               let fd2 = new FormData()
                fd2.append('data', `update sekolah set gambar_kepsek='${res.data}' where id_sekolah='1'`)//database setting di server
                fd2.append('database','infolay3_test')
                axios.post('https://infolayanans.space/api/mysql/auto.php', fd2
                , {
                    headers: {
                    Authorization: localStorage.getItem('auth._token.local')
                    }
                }).then(res => {
                    console.log(res)
                    alert("simpan gambar berhasil")
                    this.$forceUpdate()
                })
                    // get /api/gambar/url.jpg
                });
       },
      visis(){
          let fd = new FormData()
          fd.append('data', `update sekolah set visi='${this.visi}' where id_sekolah='1'`)//database setting di server
          fd.append('database','infolay3_test')
          axios.post('https://infolayanans.space/api/mysql/auto.php', fd
          , {
              headers: {
              Authorization: localStorage.getItem('auth._token.local')
              }
          }).then(res => {
              console.log(res)
              alert("simpan visi berhasil")
              this.$forceUpdate()
          })
      },
      
      sejarahs(){
           let fd = new FormData()
          fd.append('data', `update sekolah set sejarah='${this.sejarah}' where id_sekolah='1'`)//database setting di server
          fd.append('database','infolay3_test')
          axios.post('https://infolayanans.space/api/mysql/auto.php', fd
          , {
              headers: {
              Authorization: localStorage.getItem('auth._token.local')
              }
          }).then(res => {
              console.log(res)
              alert("simpan sejarah berhasil")
              this.$forceUpdate()
          })
      },
       kepalas(){
           let fd = new FormData()
          fd.append('data', `update sekolah set kepala_sekolah='${this.vdata.kepsek}' where id_sekolah='1'`)//database setting di server
          fd.append('database','infolay3_test')
          axios.post('https://infolayanans.space/api/mysql/auto.php', fd
          , {
              headers: {
              Authorization: localStorage.getItem('auth._token.local')
              }
          }).then(res => {
              console.log(res)
              alert("simpan nama kepala sekolah berhasil")
              this.$forceUpdate()
          })
      },
       sambutans(){
           let fd = new FormData()
          fd.append('data', `update sekolah set sambutan='${this.sambutan}' where id_sekolah='1'`)//database setting di server
          fd.append('database','infolay3_test')
          axios.post('https://infolayanans.space/api/mysql/auto.php', fd
          , {
              headers: {
              Authorization: localStorage.getItem('auth._token.local')
              }
          }).then(res => {
              console.log(res)
              alert("simpan nama kepala sekolah berhasil")
              this.$forceUpdate()
          })
      },
  },
  data() {
    return {
      sambutan:"",
      visi: "",
      sejarah: "",
       file: "",
       vdata:{},
       gambar:"",
       gambar2:"",
         imgs: [],
         ready:false,
    };
  }
};
</script>
